<template>
  <div class="index">
    <el-container>
      <v-header></v-header>
      <v-content></v-content>
      <el-footer><v-footer></v-footer></el-footer>
    </el-container>
  </div>
</template>

<script>
  import Header from './header/Header.vue'
  import Content from './content/Content.vue'
  import Footer from './footer/Footer.vue'

  export default {
    name: 'Index',
    components:{
      'v-header': Header,
      'v-content': Content,
      'v-footer': Footer,
    },
    data () {
      return {
        msg: ''
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



