<template>
    <div class="headerWrapper">
      <a class="logo">logo</a>
      <div class="trigger">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="CreateMusic" name="first" >
            Create Music
          </el-tab-pane>
          <el-tab-pane label="Login" name="second">
           log in
          </el-tab-pane>
          <el-tab-pane label="register" name="third">
            register
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>

</template>

<script>

  export default {
    name: 'Header',
    data () {
      return {
        activeName: 'first',
      }
    },
    methods: {
      handleClick(tab, event) {
        let label = '/' + tab.label
        this.$router.beforeEach((to, from, next) => {
          if (tab.label == 'CreateMusic') {
            window.location.href = '/static/createMusic.html'
          } else {
            next()
          }
        })
        this.$router.push(label)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .headerWrapper {
    float: right;
    line-height: 75px;
    margin-right: 0;
    margin-left: auto;
    padding-right: 30px;
    padding-left: 30px;
    height: 75px;
    border-bottom: 1px solid #f0f0f0;
  }
  .logo {
    position: absolute;
    top: 0;
    left: 10px;
    display: block;
    width: 100px;
    height: 75px;
    background-size: contain;
  }
  .trigger .page-link:not(:last-child) {
    margin-right: 20px;
  }
  .trigger .page-link {
    color: #ffffff;
    line-height: 1.5;
  }
  iframe.animation {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    border: 0;
  }
  .page-link {
    text-decoration:none;
  }

</style>



