<template>
  <div class="contentWrapper">
    <el-carousel :interval="4000" type="card" height="200px">
      <el-carousel-item v-for="(item, index) in items" :key="index">
        <img class="cardBg" :src="item" width="100%" height="100%"/>
      </el-carousel-item>
    </el-carousel>
    <div class="about">
      <el-collapse v-model="activeNames" @change="handleChange">
        <el-collapse-item>
          <template slot="title">
            <h1>description</h1>
          </template>
          <div class="el-collapse-item div">
            <v-description></v-description>
          </div>
        </el-collapse-item>
        <el-collapse-item>
          <template slot="title">
            <h1>feature</h1>
          </template>
          <div><v-feature></v-feature></div>
        </el-collapse-item>
        <el-collapse-item>
          <template slot="title">
            <h1>main function</h1>
          </template>
          <div><v-main-function></v-main-function></div>
      </el-collapse-item>
        <el-collapse-item>
          <template slot="title">
            <h1>how it works</h1>
          </template>
          <div><v-how-it-work></v-how-it-work></div>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>

</template>

<script>
  import Description from './Description.vue'
  import Feature from './Feature.vue'
  import HowItWork from './HowItWork.vue'
  import MainFunction from './MainFunction.vue'

  export default {
    name: 'Header',
    components: {
        'v-description': Description,
        'v-feature': Feature,
        'v-how-it-work': HowItWork,
        'v-main-function': MainFunction,
    },
    data () {
      return {
        msg: 'hello',
        activeNames: ['1'],
        items: [
            "https://static-cdn.ampermusic.com/static/img/index/main.jpg",
            "https://static-cdn.ampermusic.com/static/img/index/main.jpg",
            "https://static-cdn.ampermusic.com/static/img/index/main.jpg",
        ],
      }
    },
    methods: {
      handleChange(val) {

      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .contentWrapper {
    margin: 10px;

  }
  h1 {
    display: block;
    text-align:center;
    font-size: 1.5em;
    font-weight: bold;
    color: #303232;
    font-family: "Raleway",sans-serif;
    z-index: 100;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .about {
    display: block;
    font: 300 16px/1.5 "Roboto", sans-serif;
    color: #111;
    background-color: #fdfdfd;
    font-feature-settings: "kern" 1;
    font-kerning: normal;
  }
  p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
  }
  .el-collapse-item {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .el-collapse-item div {
    margin-top: 10px;
    margin-bottom: 10px;
  }

</style>



